module.exports = "/******************** Dashboard slider *************************/\n/* GLOBAL STYLES\n-------------------------------------------------- */\n/* Padding below the footer and lighter body text */\nswiper {\n  height: 200px;\n  width: 80%;\n  margin: auto;\n}\n.swiper-slide img {\n  width: 100%;\n}\n.carousel-item {\n  height: 100vh;\n}\n.bg-img {\n  height: 100vh;\n  position: absolute;\n  width: 100%;\n  top: 0;\n  left: 0;\n  background-size: cover !important;\n  transition: all 0.3s ease;\n  -webkit-transition: all 0.3s ease;\n}\n.overlay:before {\n  position: absolute;\n  content: '';\n  width: 100%;\n  height: 100%;\n  top: 0;\n  left: 0;\n  background: #292929;\n  opacity: 0.3;\n  z-index: 2;\n}\n.slide {\n  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));\n}\n.item:after {\n  content: \"\";\n  display: block;\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  background: rgba(0, 0, 0, 0.6);\n}\n/* Bootstrap Lightbox using Modal */\n#profile-grid {\n  overflow: auto;\n  white-space: normal;\n}\n#profile-grid .profile {\n  padding-bottom: 40px;\n}\n#profile-grid .panel {\n  padding: 0;\n}\n#profile-grid .panel-body {\n  padding: 15px;\n}\n#profile-grid .profile-name {\n  font-weight: bold;\n}\n#profile-grid .thumbnail {\n  margin-bottom: 6px;\n}\n#profile-grid .panel-thumbnail {\n  overflow: hidden;\n}\n#profile-grid .img-rounded {\n  border-radius: 4px 4px 0 0;\n}\n/******************* Dashboard Slider *************************/\n.img-responsive {\n  height: 100vh;\n  width: 100%;\n}\n@media only screen and (max-width: 768px) {\n  .carousel-item {\n    height: 50vh;\n  }\n  .bg-img {\n    height: 50vh;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"